import { DataStore } from './../../models/data-store';

import { RastroPage } from './../index';
import { TranslateService } from "@ngx-translate/core";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  Platform,
  ModalController,
  LoadingController
} from "ionic-angular";
import { DatePicker } from "@ionic-native/date-picker";
import {
  CalendarModalOptions,
  CalendarModal,
  CalendarResult
} from "ion2-calendar";
import { DatePipe } from "@angular/common";
import { VeiculoService } from "../../providers/api/veiculo.service";


/**
 * Generated class for the RastroSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-rastro-search",
  templateUrl: "rastro-search.html"
})
export class RastroSearchPage {
  translateString;
  nowHour: String;
  toDate: Date;
  fromDate: Date;
  fromHour: String;
  toHour: String;
  veiculo: DataStore;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private datePicker: DatePicker,
    private plt: Platform,
    public modalCtrl: ModalController,
    public translateService: TranslateService,
    public datePipe:DatePipe,
    public veiculoService:VeiculoService,
    public loadingCtrl: LoadingController
  ) {
    this.veiculo = this.navParams.get('veiculo');
    this.toDate = new Date();
    this.toHour = "23:59:59";
    this.fromHour = "00:00:00";
    this.fromDate = new Date();
    
    this.translateService
      .get(["WAIT", "CANCEL", "FROM_DATE", "TO_DATE"])
      .subscribe(values => {
        this.translateString = values;
      });
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad RastroSearchPage");
  }

  openCalendar() {
    const options: CalendarModalOptions = {
      pickMode: "range",
      title: '',
      canBackwardsSelected: true,
      to: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
      from: new Date(),
      closeLabel: this.translateString["CANCEL"],
      doneLabel: this.translateString["DONE"],
      closeIcon: true,
      doneIcon: true,
      color: 'secondary'
    };

    let myCalendar = this.modalCtrl.create(CalendarModal, {
      options: options
    });

    myCalendar.present();

    myCalendar.onDidDismiss(
      (date: { from: CalendarResult; to: CalendarResult }, type: string) => {
        if (date != null) {
          this.toDate = date.to.dateObj;
          this.fromDate = date.from.dateObj;
        }
      }
    );
  }

  rastro() {



    const filtroRastro = {
      dtIni: new Date(this.datePipe.transform(this.fromDate,"yyyy-MM-dd") + 'T' + this.fromHour) ,
      dtFim: new Date(this.datePipe.transform(this.toDate,"yyyy-MM-dd") + 'T' + this.toHour),
      veiId: this.veiculo.veiculoId,
      gsm : true,
      sat : true
    }

    let loading = this.loadingCtrl.create({
      content: this.translateString['WAIT']
    });
    loading.present();

    this.veiculoService.getRastro(filtroRastro).subscribe((res: any) => {
      console.log('teste: ',JSON.stringify(res.inspecao));
      this.veiculoService.tabRef.getByIndex(0).push(RastroPage, {
        veiculo: this.veiculo,
        rastro: res.rastro,
        inspecao: res.inspecao
      });

    }, error => {
      loading.dismiss();
    }, () => {
      loading.dismiss();
    });
  }
}



